.root {
  width: 100%;
  display: flex;
}

.inner {
  display: flex;
  position: relative;
  width: 100%;
}

.input {
  width: 100%;
  height: 50px;
  padding-left: 20px;
  padding-right: 60px;
  background: white;
  caret-color: unset;
  font-size: 16px;
  line-height: 24px;
  border: 1px solid rgb(117, 117, 117);
  border-radius: 12px;

  &:focus {
    outline: 1px solid #cacaca;
  }

  &::placeholder {
    color: #c1c1c1;
    font-size: 16px;
    line-height: 27px;
  }
}

.icon {
  height: 24px;
  width: 24px;
  cursor: pointer;
  fill: #c1c1c1;
  position: absolute;
  top: 50%;
  translate: 0% -50%;
  right: 24px;
}
