@import "../../../shared/styles/mixins/breakpoints";

.root {
  width: 100%;
  height: 100%;
}

.inner {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.content {
  min-width: 300px;
  width: 500px;
  background-color: white;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(163, 163, 163);
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0px 0px 40px 10px rgba(0, 0, 0, 0.169);
}

.title {
  font-size: 32px;
  width: max-content;
}

.toggler {
  cursor: pointer;
  width: max-content;
  color: blue;
  font-size: 15px;
  line-height: 20px;
  border-bottom: 1px solid blue;
  margin: 0 auto;
  margin-top: 40px;
}

.formWrapper {
  margin-top: 80px;
}

@include breakpoint(md) {
  .content {
    padding: 50px;
  }

  .title {
    font-size: 48px;
  }
}
