.root {
  display: block;
  width: 22px;
  height: 22px;
}

.originalCheckbox {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.customCheckbox {
  position: absolute;
  cursor: pointer;
  user-select: none;
  width: 22px;
  height: 22px;
  border: 1px solid#D6D6D6;
  background-color: rgba(255, 255, 255, 1);

  &::before {
    content: "";
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    width: 22px;
    height: 22px;
    background: url("../../assets/icons/checkmark.png");
    background-repeat: no-repeat;
    background-size: 9px 6px;
    background-position: center;
    background-color: var(--checkbox-checked-bg-color);
    transition-duration: 0.2s;
  }
}

.originalCheckbox:checked + .customCheckbox::before {
  opacity: 1;
}
